
import { FormField, FormItem, FormLabel, FormControl, FormMessage } from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { UseFormReturn } from "react-hook-form";
import { CustomerRequirementsFormData } from "@/schemas/customerRequirements";

interface TimingContactFieldsProps {
  form: UseFormReturn<CustomerRequirementsFormData>;
}

export const TimingContactFields = ({ form }: TimingContactFieldsProps) => {
  return (
    <>
      <FormField
        control={form.control}
        name="preferredTiming"
        render={({ field }) => (
          <FormItem>
            <FormLabel> </FormLabel>
            <FormControl>
              <Input {...field} placeholder="    " />
            </FormControl>
            <FormMessage />
          </FormItem>
        )}
      />

      <FormField
        control={form.control}
        name="contactMethod"
        render={({ field }) => (
          <FormItem>
            <FormLabel>  </FormLabel>
            <Select onValueChange={field.onChange} defaultValue={field.value}>
              <FormControl>
                <SelectTrigger>
                  <SelectValue placeholder="  " />
                </SelectTrigger>
              </FormControl>
              <SelectContent>
                <SelectItem value="phone"></SelectItem>
                <SelectItem value="email"> </SelectItem>
                <SelectItem value="whatsapp"></SelectItem>
              </SelectContent>
            </Select>
            <FormMessage />
          </FormItem>
        )}
      />
    </>
  );
};
